"use client";
import { assets, navData } from "@/assets/assets";
import React from "react";
import Image from "next/image";
import { Menu, X } from "lucide-react";
import { useState } from "react";
import Link from "next/link";

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="w-full py-5 px-6 md:px-10 flex justify-between items-center shadow-md bg-black z-50 fixed">
      {/* Logo */}
      <div>
        <Image className="w-28" src={assets.logo} alt="logo" />
      </div>

      {/* Desktop Nav */}
      <ul className="hidden md:flex items-center gap-7">
        {navData.map((item, index) => (
          <li
            key={index}
            className="cursor-pointer text-gray-700 hover:text-primary"
          >
            <Link href={item.url}>{item.title}</Link>
          </li>
        ))}
        <button className="border border-primary px-4 py-2 rounded-md hover:bg-primary hover:text-white transition">
          Contact
        </button>
      </ul>

      {/* Mobile Menu Button */}
      <button
        className="md:hidden text-gray-700"
        onClick={() => setIsOpen(!isOpen)}
      >
        {isOpen ? <X size={28} /> : <Menu size={28} />}
      </button>

      {/* Mobile Menu */}
      {isOpen && (
        <div className="absolute top-16 left-0 w-full bg-dark shadow-md flex flex-col px-7 py-5 md:hidden z-50">
          <ul className="flex flex-col gap-5 text-lg text-gray-700">
            {navData.map((item, index) => (
              <li key={index} className="cursor-pointer hover:text-primary">
                <Link href={item.url} onClick={() => setIsOpen(false)}>
                  {item.title}
                </Link>
              </li>
            ))}
          </ul>
          <button className="mt-4 border border-primary px-4 py-2 rounded-md text-primary hover:bg-primary hover:text-white transition">
            Contact
          </button>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
