"use client";

import React, { useEffect, useRef } from "react";
import Image from "next/image";
import { assets } from "@/assets/assets";
import gsap from "gsap";

const Header = () => {
  const headerRef = useRef(null);
  const textRef = useRef(null);
  const imageRef = useRef(null);
  const buttonsRef = useRef(null);

  useEffect(() => {
    // GSAP Animation Timeline
    const tl = gsap.timeline({ defaults: { duration: 1, ease: "power3.out" } });

    tl.from(headerRef.current, { opacity: 0 })
      .from(textRef.current, { y: 50, opacity: 0 }, "-=0.5")
      .from(imageRef.current, { x: 50, opacity: 0 }, "-=0.5")
      // Using gsap.utils.toArray to ensure both buttons animate properly
      .from(gsap.utils.toArray(buttonsRef.current), {
        y: 30,
        opacity: 0,
        stagger: 1,
      });
  }, []);

  return (
    <div
      ref={headerRef}
      className="w-full py-20 px-6 md:px-20 flex flex-col md:flex-row items-center justify-between text-center md:text-left"
    >
      {/* Right Section (Image) */}
      <div
        ref={imageRef}
        className="flex justify-center md:mr-10 md:justify-end md:mt-0 md:w-1/2 order-1 md:order-2"
      >
        <Image src={assets.hero} alt="hero" className="w-[80%] md:w-[60%]" />
      </div>

      {/* Left Section (Text and Buttons) */}
      <div ref={textRef} className="md:w-1/2 order-2 md:order-1">
        <div className="py-10">
          <h1 className="font-bold text-2xl md:text-3xl text-primary md:pb-5">
            Hello, I'm Ellas Touray
          </h1>
          <h2 className="text-4xl md:text-[80px] font-bold leading-[75px]">
            Graphic Designer
          </h2>
        </div>

        <p className="text-sm md:text-base text-lightGray">
          Hi, I’m Ellas, a passionate designer with three years of experience
          creating unique visuals for businesses of all sizes. During my
          internship at Android Make Your Mark, I refined my graphic design
          skills and expanded my creativity. Currently, I’m studying Graphic and
          Digital Media at AP University of Applied Sciences, building on a
          strong multimedia and print technology background. I thrive on
          challenges and am always eager to learn, grow, and refine my craft.
          Let’s create something amazing together!
        </p>

        <div
          ref={buttonsRef}
          className="mt-10 flex flex-wrap items-center justify-center md:justify-start gap-3"
        >
          {/* Email Me Button */}
          <a
            href="/"
            className="bg-primary px-5 py-2 rounded-md md:hover:bg-transparent md:border-2 md:border-primary transition"
          >
            Email Me
          </a>

          {/* Download CV Button */}
          <button className="flex items-center text-lightGray">
            <Image className="mr-2" src={assets.download} alt="download-icon" />
            Download CV
          </button>
        </div>
      </div>
    </div>
  );
};

export default Header;
